import mongoose from "mongoose";
import bluebird from 'bluebird';
mongoose.Promise = bluebird;
import environment from "../environment.js";

let conn = mongoose.createConnection(environment)

// the middleware function
module.exports = function () {

    // create schema
    let formStructure = mongoose.Schema({}, {
        collection: "formStructure",
        strict: false,
        timestamps: true
    });

    let formData = mongoose.Schema({}, {
        collection: "formData",
        strict: false,
        timestamps: true
    })

    let form = conn.model("STRUCTURE", formStructure);
    let form_data = conn.model("FORMDATA", formData)
    return function (req, res, next) {
        req.formStructure = form;
        req.formData = form_data;
        next();
    };
};